  const allRotations = {
    axeX: {
      U: "B",
      D: "F",
      R: "R",
      L: "L",
      F: "U",
      B: "D",
      u: "b",
      d: "f",
      r: "r",
      l: "l",
      f: "u",
      b: "d",
      x: "x",
      y: "z'",
      z: "y'"
    },
    axeXPrime: {
      U: "F",
      D: "B",
      R: "R",
      L: "L",
      F: "D",
      B: "F",
      u: "f",
      d: "b",
      r: "r",
      l: "l",
      f: "d",
      b: "f",
      x: "x",
      y: "z",
      z: "y"
    },

    axeY: {
      U: "U",
      D: "D",
      R: "F",
      L: "B",
      F: "L",
      B: "R",
      u: "u",
      d: "d",
      r: "f",
      l: "b",
      f: "l",
      b: "r",
      x: "z'",
      y: "y",
      z: "x'"
    },
    axeYPrime: {
      U: "U",
      D: "D",
      R: "B",
      L: "F",
      F: "R",
      B: "L",
      u: "u",
      d: "d",
      r: "b",
      l: "f",
      f: "r",
      b: "l",
      x: "z",
      y: "y",
      z: "x"
    },

    axeZ: {
      U: "R",
      D: "L",
      R: "D",
      L: "U",
      F: "F",
      B: "B",
      u: "r",
      d: "l",
      r: "d",
      l: "u",
      f: "f",
      b: "b",
      x: "y'",
      y: "x",
      z: "z"
    },
    axeZPrime: {
      U: "L",
      D: "R",
      R: "U",
      L: "D",
      F: "F",
      B: "B",
      u: "l",
      d: "r",
      r: "u",
      l: "d",
      f: "f",
      b: "b",
      x: "y",
      y: "x'",
      z: "z"
    }
  };

  export default class MoveAnalyzer {

    opposite(move) {
      if (move.indexOf("'") === -1) {
        return move + "'";
      }
      return move.replace("\'", '');
    }

    _rotate(move, axeRotations) {
      const {
        unit,
        extra
      } = this.destructure(move);
      return this.normalize(axeRotations[unit] + extra);
    }

    rotate(move, axe) {
      let result;
      if (axe === 'x') {
        result = this._rotate(move, allRotations.axeX);
      } else if (axe === "x'") {
        result = this._rotate(move, allRotations.axeXPrime);
      } else if (axe === "y") {
        result = this._rotate(move, allRotations.axeY);
      } else if (axe === "y'") {
        result = this._rotate(move, allRotations.axeYPrime);
      } else if (axe === "z") {
        result = this._rotate(move, allRotations.axeZ);
      } else if (axe === "z'") {
        result = this._rotate(move, allRotations.axeZPrime);
      } else {
        result = "fuck !";
      }
      return result;
    }

    destructure(move) {
      const moveRegex = /([A-Za-z])(['|\d]{0,2})/g;

      const moves = {};
      let temp;
      while ((temp = moveRegex.exec(move)) !== null) {
        moves.unit = temp[1];
        moves.extra = temp[2];
      }
      return moves;
    }

    normalize(move) {
      return move.replace("''", "");
    }

  }



